body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background: #242c2f;
}

.header{
  background: #23282d;
  padding: 70px 50px 30px 50px;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  max-width: 1600px;
  margin: 0 auto;
}

.indicator-progress-wrap{
  width: 70%;
}

.out-indicator-progress{
  height: 20px;
  background: #BEBEBE;
}

.indicator-progress{
  height: 100%;
  background: #E83F35;
  transition: width .5s;
}

.indicator-text{
  display: inline-block;
  color: #eeeeee;
  margin-bottom: 7px;
  border-bottom: 1px dotted #e83f35;
}

.fa{
  color: #9ABEC4;
}

.button{
  width: 60px;
  height: 60px;
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}

.button-fast-forward,
.button-home{
  margin-top: 20px;
}

.button-icon{
  font-size: 50px;
}

.progress-block{
  display: flex;
  justify-content: space-around;
  align-items: center;
}

hr{
  margin: 50px 0;
  border: 1px solid #9ABEC4;
}

.vocabulary{
  margin: 60px 0;
}

.vocabulary-text{
  color: #FDFDFD;
  font-size:30px;
}

.vocabulary-header{
  display: flex;
  justify-content: center;
}

.footer{
  display: flex;
}

.footer-item{
  font-size: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  width: 25%;
}

.pink{
  background: #FF9695;
}

.orange{
  background: #FEC396;
}

.blue{
  background: #70C0C0;
}

.green{
  background: #83DF83;
}

.border-rigtht-white{
  border-right: 2px solid #eee;
}


/*media query*/
@media (max-width: 500px) {
  .button-play{
    margin-top: -10px;
  }

  .vocabulary-text{
    font-size: 25px;
  }
  .footer-item {
    font-size: 25px;
  }
  .indicator-text{
    font-size: 12px;
  }
}

@media (max-width: 400px) {
  .footer-item {
    font-size: 20px;
  }
  .indicator-text{
    font-size: 10px;
  }
}
